@import url(https://fonts.maateen.me/bensen/font.css);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  /* overflow-x: hidden; */
}
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #ffffff; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #acacac;
  border-radius: 1px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #566a74; 
}

:root {
  /* fonts */
  --openSans: "Open Sans";

 



  /* color */
  --primaryColor: '#5D5D5D';
  --mainTransition: all 0.2s linear;
  --textShadow: 0 1px 0 rgba(0, 0, 0, 0.4);
  --boxShadow: 1px 1px 2px 2px rgba(192, 192, 192, 0.5);
  --darkShadow:   0 1px 2px rgba(0,0,0,0.07), 
                  0 2px 4px rgba(0,0,0,0.07), 
                  0 3px 8px rgba(0,0,0,0.07), 
                  0 2px 8px rgba(0,0,0,0.07),
                  0 2px 2px rgba(0,0,0,0.07), 
                  0 2px 8px rgba(0,0,0,0.07);
  --bottomShadow: 0 10px 5px -8px rgba(0,0,0,.4);
  --bangleFont: 'BenSen', Arial, sans-serif;
}

