}
.tab__contentLeftTop:before,
.tab__contentLeftBottomImg:before,
.tab__contentRightImg:before {
    content: '\A';
    position: absolute;
    width: 100%; height:100%;
    top:0; left:0;
    background:rgba(0,0,0,0.6);
    opacity: 0.8;
    transition: .3s;
}
.tab__contentLeftTop:hover:before,
.tab__contentLeftBottomImg:hover:before,
.tab__contentRightImg:hover:before {
